<template>
    <div style="height:100%;width:100%">
        <el-container>
            <el-aside class="menu-left-aside" :width="showchild==1 && menuChilds!='' ? '190px' : '80px'">
                <el-container v-bind:menuAction="menuAction">
                    <el-aside class="menu-left" width="80px">
                        <div class="logo">
                            <img src="/miniapp/shop/img/logo.png"/>
                        </div>
                        <div class="nav">
                            <ul>
                                <li v-for="(item,index) in menus" v-bind:key="index"
                                    :class="item.selected==1 ? 'on' : ''">
                                    <router-link :to="item.path">
                                        <i v-bind:class="'iconfont ' + item.icon"></i>{{item.title}}
                                    </router-link>
                                </li>
                            </ul>
                        </div>
                    </el-aside>
                    <el-main v-if="showchild==1 && menuChilds!=''" class="menu-right">
                        <div class="second-sidebar-title">
                            {{childDesc}}
                        </div>
                        <div v-for="(item,index) in menuChilds" v-bind:key="index" class="item">
                            <div v-if="item.showchild && item.show==1" class="second-sidebar-subtitle ">
                                {{item.title}}
                            </div>
                            <div v-if="item.showchild && item.show==1" class="second-sidebar-sub-item">
                                <ul>

                                    <li v-for="(it,aindex) in item.children" v-bind:key="aindex"
                                        :class="it.selected==1?'on' :''" v-show="it.show==1">
                                        <router-link :to="it.path">{{it.title}}</router-link>
                                    </li>
                                </ul>
                            </div>
                            <div v-if="!item.showchild && item.show==1" class="second-sidebar-sub-item">
                                <ul>
                                    <li :class="item.selected==1 ? 'on' :''">
                                        <router-link :to="item.path">{{item.title}}</router-link>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </el-main>
                </el-container>
            </el-aside>
            <el-main style="background:#f2f2f2;min-width:1000px;">
                <router-view></router-view>
            </el-main>
        </el-container>
    </div>
</template>

<script>
    import menus from '@/menu.js';
 

    export default {
        name: 'frame',
        components: {},
        data: function () {
            return {
                tab:[],
                menus: menus.menus,
                menuChilds: [], // 下级菜单
                childDesc: '',
                showchild:0,
            };
        },
        watch: {
            '$route': 'getPath' //监听route的变化
        },
        computed: {
            menuAction: function () {
                return 'YES';
            }
        },
        created() {
            this.getPath();
        },
        methods: {
            getPath() {
                var menuChilds = [];
                var showchild = 0;
                for (var a in this.menus) {
                    if (this.menus[a].name == this.$route.name) {
                        this.menus[a].selected = 1;
                        this.childDesc = this.menus[a].desc;
                        showchild = this.menus[a].showchild;
                        menuChilds = this.menus[a].children ? this.menus[a].children : [];  // 赋值二级菜单
                    } else {
                        this.menus[a].selected = 0;
                        if (this.menus[a].children) {
                            for (var b  in this.menus[a].children) {
                                if (this.menus[a].children[b].name == this.$route.name) { // 如果第二级的相等
                                    this.menus[a].children[b].selected = 1;
                                    this.menus[a].selected = 1; //第一级SELECTED选中
                                    menuChilds = this.menus[a].children; // 二级菜单赋值
                                    this.childDesc = this.menus[a].desc;
                                     showchild = this.menus[a].showchild;
                                } else {
                                    this.menus[a].children[b].selected = 0;
                                    if (this.menus[a].children[b].children) { //第三级菜单
                                        for (var c  in  this.menus[a].children[b].children) {
                                            this.menus[a].children[b].children[c].selected = 0;
                                            if (this.menus[a].children[b].children[c].name == this.$route.name) {//如果相等
                                                this.menus[a].children[b].children[c].selected = 1;
                                                this.menus[a].children[b].selected = 1;       //第二级的赋值选中
                                                this.menus[a].selected = 1; //第一级SELECTED选中
                                                menuChilds = this.menus[a].children; // 二级菜单赋值
                                                this.childDesc = this.menus[a].desc;
                                                 showchild = this.menus[a].showchild;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                this.menuChilds = menuChilds;
                this.showchild = showchild;
            },
        },
    }
</script>
<style scoped>


    .el-container {
        height: 100%;
    }

    .menu-right {
        background: #fff;
        bottom: 0;
        box-shadow: 0 0 1px 0 rgba(0, 0, 0, .2);
        box-sizing: border-box;
        left: 80px;
        position: fixed;
        top: 0;
        width: 110px;
    }

    .el-main {
        padding: 0;
    }

    .menu-left-aside {
        top: 0;
        left: 0;
        bottom: 0;
   
    }

    .menu-left-aside .menu-left {
        background: #005DE5
    }

    .menu-left-aside .logo {
        width: 40px;
        height: 40px;
        margin: 0 auto;
        margin-top: 20px;
    }

    .menu-left-aside .logo img {
        max-width: 100%;
    }

    .menu-left-aside .nav {
        font-size: 14px;
        margin-top: 20px;
    }

    .menu-left-aside .nav li {
        height: 80px;
        color: #FFFFFF;
        border-top: 1px solid rgba(0, 0, 0, .1);
        text-align: center;
    }

    .menu-left-aside .nav a {
        color: #FFFFFF;
        display: block;
        opacity: 0.6;
    }

    .menu-left-aside .nav i {
        display: block;
        font-size: 20px;
        line-height: 30px;
        margin-top: 16px;
        /*opacity: 0.7;*/

    }

    .menu-left-aside .nav li:hover, .menu-left-aside .nav li.on {
        background: rgba(0, 0, 0, .1);

    }

    .menu-left-aside .nav li:hover a, .menu-left-aside .nav li.on a{
        opacity: 1.0;
    }


    .second-sidebar-title {
        text-align: center;
        font-size: 20px;
        height: 50px;
        line-height: 50px;
        color: #29323A;
        font-weight: 600;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        border-bottom: 1px solid #f2f2f2;
    }

    .second-sidebar-subtitle {
        padding: 20px 10px 0;
        margin: -5px 10px 0;
        font-size: 12px;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        color: #999;

    }

    .menu-right .item .second-sidebar-subtitle {
        border-top: 1px solid #eee;
    }

    .second-sidebar-title + .item .second-sidebar-subtitle {
        border-top: none;
        margin-top: 0;
    }

    .second-sidebar-sub-item {
        padding: 0px 10px;
        margin-top: 10px;
    }



    .second-sidebar-sub-item li {
        height: 30px;
        line-height: 30px;
        margin-bottom: 9px;
        overflow: hidden;
        cursor: pointer;
        border-radius: 2px;
        font-size: 14px;
        padding-left: 10px;
    }

    .second-sidebar-sub-item li a {
        color: #333;
    }

    .second-sidebar-sub-item li.on {
        background: rgba(0, 93, 229, .1);
    }

    .second-sidebar-sub-item li.on a {
        color: #005DE5;
    }

</style>
